import RecapatchaInterface from "./recaptcha";
import Errors, { CbError } from '@/hosted_fields/common/errors';
import t from '@/hosted_fields/common/locale';

declare var grecaptcha;
export default class RecaptchaUtil implements RecapatchaInterface {
  public recaptchaKey: string;
  public recaptchaReady: boolean = false;
  public isGlobalRecaptcha: boolean = false;
  public hideBanner: boolean = false;
  static MAX_RETRIES = 3;
  static DEFAULT_TIMEOUT = 10000; // in ms
  
  constructor(key: string, isGlobalRecaptcha: boolean, hideBanner: boolean) {
    this.recaptchaKey = key;
    this.isGlobalRecaptcha = isGlobalRecaptcha;
    this.hideBanner = hideBanner;
  }

  init(): Promise<any> {
    return new Promise<void>((resolve, reject) => {
      if (this.recaptchaKey)
        return this.loadRecaptcha()
          .then(() => {
            this.recaptchaReady = true;
            resolve();
          });
    });
  }

  generateToken(action): Promise<any> {
    return new Promise((resolve, reject) => {
      let retryCounter = 0;
      const doReject = () => reject(new CbError(t(Errors.recaptchaInitiationTimeout)));
      let timer;

      let resetTimer = () => {
        timer = setTimeout(() => {
          retryCounter++;
          if(retryCounter>RecaptchaUtil.MAX_RETRIES) {
            return doReject();
          } else {
            generateToken();
          }
        }, RecaptchaUtil.DEFAULT_TIMEOUT);
      }

      let generateToken = () => {
        resetTimer();
        
        // v3 implementation is working only for v3 site keys.
        grecaptcha.execute(this.recaptchaKey, { action }).then((token) => {
          clearTimeout(timer)
          resolve(token)
        });
      }

      generateToken();
    });
  }

  private loadRecaptcha(): Promise<any> {
    const script = document.createElement('script');
    script.src = this.getRecaptchaURL();
    return new Promise<void>((resolve, reject) => {
      setTimeout(() => reject(new CbError(t(Errors.recaptchaLoadTimeout))), 30000)
      script.onload = () => {
        grecaptcha.ready(() => {
          if(this.hideBanner) {
            let recaptchaBanner = document.querySelector(".grecaptcha-badge");
            if(recaptchaBanner){
            (<HTMLElement>recaptchaBanner).style.visibility = "hidden"
            }
          }
          resolve();
        });
      }
      document.head.appendChild(script);
    });
  }

  private getRecaptchaURL() : string {
    let url = `https://www.google.com/recaptcha/api.js?render=${this.recaptchaKey}`;
    
    if (typeof this.isGlobalRecaptcha == 'boolean' && this.isGlobalRecaptcha == true) {
        url = `https://www.recaptcha.net/recaptcha/api.js?render=${this.recaptchaKey}`;
    }
    return url;
  }
}
